<template lang="pug">
.frow
  .event-image
    EventImage(:img="event.imagePATH")
  .content
    EventContent(:event="event")

</template>

<script>
import { mapState } from 'vuex';
import EventImage from '@/components/event/EventImage';
import EventContent from '@/components/event/EventContent';
export default {
  name: 'Event',
  components: {
    EventImage,
    EventContent,
  },
  data() {
    return {};
  },
  computed: {
    ...mapState('parties', {
      parties: state => state.parties,
    }),
    event() {
      return this.parties.find(party => party.id === this.$route.params.id);
    },
  },
};
</script>

<style scoped lang="sass">
.event-image
  z-index: $z-page

.content
  margin-top: 100px
  z-index: $z-header-dropdown
</style>
